 .post {
     max-width: 500px;
     display: flex;
     flex-direction: column;
     justify-content: space-between;
     background-color: white;
     border: 1px solid lightgray;
     margin-bottom: 45px;
     margin-left: 20px;
 }
 .post__image {
     width: 100%;
     object-fit: contain;
     border-top: 1px solid lightgray;
     border-bottom: 1px solid lightgray;
 }

 .post__text {
     font-weight: normal;
     padding: 20px;
 }

 .post__header {
     display: flex;
     align-items: center;
     padding: 20px;
 }

 .post__avatar {
     margin-right: 10px;
 }

 .post__commentBox {
    /* position: absolute; */
    bottom: 0;
    display: flex;
    margin-top: 10px;
     
 }

 .post__input {
     flex: 1;
     border: none;
     padding: 10px;
     border-top: 1px solid lightgray;
 }

 .post__button {
     flex: 0;
     border-top: 1px solid lightgray;
     border: none;
     color: #6082a3;
 }
